* { margin:0; padding:0; box-sizing:border-box; }

html,body {
    min-height: 100%;
}
body {
	background-image: linear-gradient(rgb(244, 127, 255),rgb(254, 255, 192));
}

div {
	padding: 20px;

}

.pink img {
    float: right;
    height: 16px;
    width: 32px;
    padding-left: 10px;
}

#todo {
	width:35%;
	margin:0 auto;
	padding: 0px;
	border:1px solid lightgrey;
	border-radius: 5px;
	
}

.todos{
	color:rgb(220, 42, 236);
	font-weight: bold;
	word-wrap: break-word;
}

li {
	padding-bottom: 10px;
	font-style: oblique;
	text-align: left;
}

li span {
	font-size: 25px;
}

.tips li{
	list-style-type: none;
}

.pink i{
	margin-left: 15px;
}

span{
	font-size: 20px;
}

h1 {
	color:white;
	font-size: 40px;
}

button {
	padding:20px;
	width:30%;
	margin:5px;
	border-radius: 5px;
	background-color: rgb(251, 252, 209);
	color:grey;
	font-style: italic;
	outline:none;
	text-align: center;
}

.tips button {
	margin-top: 5px;
}


input {
	width:100%;
	height:60px;
	padding: 10px;
	font-size:15px;
	outline:none;
	background-color: rgb(254, 255, 192);
	border:none;
	color:rgb(220, 42, 236);
}

.pink {
	background-color: rgb(219, 107, 230);
	border-radius: 5px 5px 0px 0px;
	text-align: center;
}

.white {
	background-color: rgb(237, 250, 239);
	overflow:hidden;
	border-radius: 0px 0px 5px 5px ;
	text-align: center;
}

.buttonContainer {
	padding:0;
}

#tip {
	margin-top: 20px;
	display:none;
}


.counter{
	padding: 5px;
	float:right;
}

.itemSpan {
	cursor:pointer;
}

@media only screen and (max-width: 1040px) {
    button {
		width: 28%;
		padding: 20px 20px 20px 15px;
    }
    #todo {
        width: 99%;
	}
	
	.tips button {
		width:30%;
	}
   
}


